import React from 'react'
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';
import './App.css';
import './FeaturedThumbnail.css';


export default function FeaturedThumbnail(props) {
    return (
        <div>
            <motion.div whileHover={{scale: 1.02}} whileTap={{scale: 1.10}} className="project featuredDiv col-md-12">
                <Link to={props.link}>
                    <div className="project-image">
                        <img className="project-image-src" src={props.image} alt="project image"></img>
                    </div>
                    <div className="project-title">{props.title}</div>
                    <div className="project-desc">{props.desc}</div>
                </Link>
            </motion.div>
        </div>
    )
}
