import React from 'react'
import {motion} from 'framer-motion';
import { Link } from 'react-router-dom';

import './App.css'


export default function Thumbnail(props) {
    return (
        <div>
            <motion.div whileHover={{scale: 1.02}} whileTap={{scale: 0.98}} className="project-small featuredDiv col-xs-12 col-sm-6 col-md-4">
                <Link to={props.link}>
                    <div className="project-image-small">
                        <img className="project-image-src-small" src={props.image} alt="project image small"></img>
                    </div>
                    <div className="project-title-small">{props.title}</div>
                    <div className="project-desc-small">{props.desc}</div>
                </Link>
            </motion.div>
        </div>
    )
}
