import React from 'react'
import './PortfolioDetail.css';

import {motion} from 'framer-motion';

export default function PortfolioDetail(props) {
    return (
        <motion.div initial={{opacity: 0}} animate={{opacity: 1}} exit={{opacity: 0}} className="row detailRow">
            <div className="col-md-4 col-xs-12 col-sm-12">
                <img src={props.image} className="detailImg" />
            </div>
            <div className="col-md-6 col-xs-12 col-sm-12">
                <h3 className="detailTitle">{props.title}</h3>
                <h5 className="detailLang">{props.langs.map(languages => (
                    <span key={languages} className="languages">{languages}.</span>
                ))}</h5>
                <h6 className="detailDate">{props.date}</h6>
                <a href={props.link} target="_blank" className="detailLink">{props.link}</a>
                <p>{props.desc}</p>
            </div>
        </motion.div>
    )
}
